.header {
  background: url("../img/header-bg.png") 60% center no-repeat, linear-gradient(293.35deg, #00CFAB 28.55%, #DCFF37 72.26%);
}

.header__wrap {
  padding-top: 1px;
  padding-bottom: 1px;
  position: relative;
  padding-left: 600px;
}

.header__img {
  position: absolute;
  left: 0;
  top: 10px;
}

.header__course {
  text-align: left;
  padding-top: 60px;
}

.header__author {
  font-size: 16px;
  line-height: 22px;
  color: $white;
  margin: 0 0 25px 0;
}

.header__videocourse {
  width: 170px;
  line-height: 40px;
  background-color: #ED1C24;
  font-size: 22px;
  text-align: center;
  margin: 20px 0;
  color: $white;
  font-weight: 800;
  font-family: 'Montserrat';
}

.header__name {
  font-family: Montserrat;
  font-weight: 800;
  line-height: 57px;
  font-size: 47px;
  color: $white;
  margin: 20px 0;
}

.header__menu {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin-left: -90px;
  margin-top: 65px;
  margin-bottom: 25px;
}

.header__menu-item {
  
  a {
    font-size: 16px;
    line-height: 22px;
    color: $white;
    text-decoration: none;
    
    &:hover {
      text-decoration: underline;
    }
    
  }
  
}

@media (max-width: 1100px) {

  .header__img {
    width: 450px;
    left: 10px;
  }

  .header__wrap {
    padding-left: 500px;
  }

}