.content {
  max-width: 970px;
  margin: 0 auto;
  background-color: $white;

  .content__bonus {

    .content__section {
      background: linear-gradient(180deg, #07DFB8 0%, #1A77D7 100%);

      .section__name {
        display: block;
        text-align: left;
        margin-top: 0;

        &::before {
          content: 'Бонус';
          display: block;
          width: 94px;
          color: #0BC7BF;
          box-sizing: border-box;
          margin-bottom: 20px;
        }

      }

      .section__show {
        margin-top: 0;
      }

    }

  }

  .content__files {

    .content__section {
      background: none;


      .section__name {
        color: $black;
        font-size: 40px;
        line-height: 49px;

        &::before {
          background-color: transparent;
          content: '';
        }

      }

    }

  }

  .content__title {
    font-size: 47px;
    line-height: 57px;
    font-weight: 800;
    text-align: center;
    color: $black;
    font-family: 'Montserrat';
    margin-top: 80px;
    margin-bottom: 30px;
  }

}