@mixin clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 $padding;
  box-sizing: border-box;
  margin: 0 auto;
}

@mixin wrap($w) {
  width: $w;
  margin: 0 auto;
}

@mixin mini-button($color, $fontcolor, $border: false, $percenthover: 15%) {
  width: 170px;
  box-sizing: border-box;
  color: $fontcolor;
  font-size: 16px;
  line-height: 26px;
  padding: 4px 0 5px;
  text-decoration: none;
  font-weight: 700;
  display: block;
  text-align: center;
  border-radius: 5px;

  @if $border == true {
    border: 2px solid $color;
  } @else if $border == false {
    background-color: $color;
  }


  &:hover {
    background-color: darken($color, $percenthover);
    @if $border == true { border-color: darken($color, $percenthover);  }
  }

}