.section {
  margin: 30px 0;
  background: linear-gradient(306.96deg, #00CFAB 25.12%, #DCFF37 101.78%), #00CFA7;
  border-radius: 5px;
  padding-top: 35px;

  .section__img {
    display: block;
    margin: 0 auto;
  }

  .section__name {
    font-weight: 700;
    line-height: 41px;
    font-size: 30px;
    text-align: center;
    color: $white;
    margin: 10px 0 15px;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
      content: 'Раздел 1.';
      counter-increment: item;
      font-weight: 700;
      font-size: 20px;
      color: #6EE770;
      line-height: 32px;
      background-color: $white;
      display: inline-block;
      padding: 0 15px;
      flex-shrink: 0;
      margin-right: 20px;
    }

  }

  .section__show {
    text-align: center;
    line-height: 22px;
    font-size: 16px;
    color: $white;
    padding: 0 0 20px;
    margin: 30px 0 0 0;

    span {
      cursor: pointer;
    }

    a {
      color: $white;
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
      
    }


  }

  .section__lessons {
    background-color: #F8F9FB;
    border-radius: 5px;
    padding-top: 30px;
    padding-bottom: 30px;
    display: none;
  }

  .section__lesson {
    margin: 15px 0;

    div {
      font-size: 20px;
      line-height: 28px;
      color: #14D1BA;
      margin-left: 169px;
      
      a {
        color: $black;
        text-decoration: underline;
        display: inline-block;
        line-height: 28px;
        font-size: 20px;
        margin-left: 5px;
        
        &:hover {
          text-decoration: none;
        }
        
      }

    }

  }

}

.section__block {
  display: flex;
  align-items: center;
}

.section__left {
  margin-left: 105px;
  margin-right: 50px;
}

.section__homework {
  margin-left: 145px;
  margin-top: 30px;
  position: relative;

  p {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 140%;
    color: #14D1BA;
  }

  .practice {
    border-radius: 5px;
    text-decoration: none;
    color: $white;
    width: 160px;
    line-height: 40px;
    text-align: center;
    box-sizing: border-box;
    background-color: #14D1BA;
    font-weight: 700;
    margin-left: 10px;

    span {
      background: url("../img/icon-homework.svg") left center no-repeat;
      padding-left: 25px;
    }

    &:hover {
      background-color: darken(#14D1BA, 10%);
    }

  }

  .section__exercise {
    background-color: #D7D7D7;
    border-radius: 10px;
    padding: 20px 25px;
    width: 420px;
    box-sizing: border-box;
    position: absolute;
    margin-left: 0;
    left: 0;
    top: 60px;
    display: none;
    z-index: 10;

    &:before {
      content: "";
      border: 18px solid transparent;
      border-bottom: 30px solid #D7D7D7;
      border-top-width: 1px;
      position: absolute;
      top: -27px;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 8;
    }

    p {
      margin: 10px 0;
      color: $black;
      font-size: 16px;
      line-height: 20px;
    }

    a {
      font-size: 16px;
      line-height: 20px;
      color: $fourtly;
      text-decoration: underline;
      margin-left: 0;

      &:hover {
        text-decoration: none;
      }

    }
  }

}

.section__materials {
  display: flex;
  justify-content: space-between;
  margin: 70px 0 80px;
}

.section__material {

  a {
    width: 230px;
    line-height: 50px;
    background-color: #14D1BA;
    border-radius: 5px;
    font-size: 18px;
    color: $white;
    display: block;
    text-decoration: none;
    font-weight: 700;
    text-align: center;

    &:hover {
      background-color: darken(#14D1BA, 10%);
    }

  }

  &:nth-child(1) {

    span {
      padding-left: 30px;
      background: url("../img/source.svg") left center no-repeat;
    }

  }

  &:nth-child(2) {

    a {
      width: 262px;
    }

    span {
      padding-left: 30px;
      background: url("../img/icons.svg") left center no-repeat;
    }

  }

  &:nth-child(3) {

    span {
      padding-left: 30px;
      background: url("../img/spravka.svg") left center no-repeat;
    }

  }

}

.section {

  @for $i from 1 through 10 {
    &:nth-of-type(#{$i}) {

      .section__name {

          &::before {
            content: 'Раздел #{$i}.';
          }

      }

    }

  }

}