$lightgray: #f1f4f7;

.course {
  margin: 20px 0;

  p {
    font-size: 20px;
    font-weight: 300;
    margin: 15px 0;
    line-height: 24px;

    + .course__element {
      margin-top: 0;
    }

  }

  .course__element {
    background-color: $lightgray;
    padding: 25px 70px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0;
  }

  .course__img {
    flex-basis: 230px;
    text-align: center;
    flex-shrink: 0;

    img {
      display: block;
      max-width: 100%;
    }

  }

  .course__txt {
    flex-basis: calc(100% - 300px);
  }

  .course__title {
    margin: 0 0 5px 0;
    font-family: Montserrat;
    font-weight: 800;
    line-height: normal;
    font-size: 20px;
  }

  .course__author {
    color: #7a8694;
    font-size: 14px;
    font-weight: 300;
    margin: 5px 0;
  }

  .course__icons {
    display: flex;
    align-items: center;
    margin: 20px 0 15px;

    p {
      font-size: 14px;
      line-height: 26px;
      font-weight: 300;
      margin: 0;
      padding: 5px 0;
    }

    .course__lesson {
      background: url('/img/lesson.png') left center no-repeat;
      padding-left: 40px;
      margin-right: 40px;
    }

    .course__time {
      background: url('/img/time.png') left center no-repeat;
      padding-left: 30px;
    }

  }

  .course__excerpt {
    color: $dark-gray;
    font-size: 16px;
    line-height: 24px;

    span {
      font-size: 14px;
      font-style: italic;
      display: block;
      line-height: 20px;
    }

  }

  .course__buttons {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
  }

  .course__input {
    width: 213px;
    margin: 15px 0;

    input {
      width: 100%;
      border: 1px solid $blue;
      font-size: 14px;
      font-weight: 300;
      color: $dark-gray;
      line-height: 16px;
      padding: 7px 0 7px 40px;
      box-sizing: border-box;
    }

    ::-webkit-input-placeholder {
      color: $dark-gray;
    }

    :-moz-placeholder {
      color: $dark-gray;
    }

    ::-moz-placeholder {
      color: $dark-gray;
    }

    :-ms-input-placeholder {
      color: $dark-gray;
    }

  }


  .course__input--person {

    input {
      background: url('../img/person.png') 12px 8px no-repeat;
    }

  }

  .course__input--email {

    input {
      background: url('../img/mail.png') 12px 7px no-repeat;
    }

  }

}

.course--two-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .course__element {
    flex-wrap: wrap;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    background-color: $white;
    flex-basis: auto;
    width: 49%;
    padding: 23px 20px;
    position: relative;
    margin: 12px 0;
  }

  .course__title {
    flex-basis: 100%;
    font-size: 20px;
    margin: 0;
  }

  .course__author {
    flex-basis: 100%;
    margin-bottom: 0;
  }

  .course__free {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 14px;
    line-height: 26px;
    background-color: #8dc63f;
    padding: 0 10px;
    color: $white;
    font-weight: 700;
    margin: 0;
  }

  .course__buttons {
    flex-wrap: wrap;
  }

  .course__txt {
    flex-basis: calc(100% - 265px);
  }

  .course__icons {
    flex-wrap: wrap;
    margin: 0;

    .course__lesson {
      flex-basis: 100%;
      margin-right: 0;
    }

    .course__time {
      flex-basis: 100%;
      padding-left: 40px;
      background-position: 4px center;
    }

  }

  .course__excerpt {
    font-size: 14px;
    line-height: 21px;
    margin: 10px 0;
    color: #586377;
  }

  .course__buttons {
    margin: 0;

    a {
      margin: 5px 0;
    }

  }

  .course__price {
    font-size: 16px;
    line-height: 26px;
    font-weight: 700;
    color: $dark-gray;

    span {
      font-size: 20px;
      color: $black;
      margin-left: 5px;
    }

  }

}

@media screen and (max-width: 1124px) {

  .course {

    .course__element {
      padding: 25px;
    }

    .course__buttons {
      flex-wrap: wrap;

      a {
        flex-basis: 32%;
      }

    }

  }

  .course--two-block {

    .course__element {
      padding: 23px 15px;
    }

    .course__buttons {

      a {
        flex-basis: auto;
      }

    }

  }

}

@media screen and (max-width: 1024px) {

  .course--two-block {

    .course__element {
      width: 100%;
    }

    .course__buttons {
      justify-content: flex-start;

      a {
        margin: 10px 15px 10px 0;

        &:first-child {
          margin: 10px 15px 10px 0;
        }

        &:last-child {
          margin: 10px 0;
        }

      }

    }

    .course__author {
      margin: 5px 0 10px;
    }

    .course__input {
      margin: 15px 0;

      &:last-child {
        margin-bottom: 5px;
      }

    }

  }

}

@media screen and (max-width: 980px) {

  .course {

    .course__txt {
      flex-basis: calc(100% - 260px);
    }

    .course__buttons {
      justify-content: flex-start;

      a {
        flex-basis: 213px;
        margin: 10px 15px 10px 0;

        &:last-child {
          margin-right: 0;
        }

      }

    }

  }

}


@media screen and (max-width: 880px) {

  .course {

    p {
      font-size: 16px;
      line-height: 20px;
    }

    .course__excerpt {
      line-height: 20px;
    }

  }

}

@media screen and (max-width: 680px) {

  .course {

    .course__title {
      font-size: 20px;
      line-height: 24px;
    }

    .course__element {
      flex-wrap: wrap;
    }

    .course__img, .course__txt {
      flex-basis: 100%;
    }

    .course__img {
      margin: 10px 0;

      img {
        margin: 0 auto;
        width: auto !important;
        max-width: 260px;
      }

    }

  }

  .course--two-block {

    .course__title {
      line-height: 28px;
    }

  }

}

@media screen and (max-width: 480px) {

  .course {

    .course__buttons {

      a {
        margin: 5px auto;

        &:last-child, &:first-child {
          margin: 10px auto;
        }

      }

    }

    .course__icons {
      flex-wrap: wrap;

      .course__lesson {
        flex-basis: 100%;
        margin-right: 0;
      }

      .course__time {
        flex-basis: 100%;
        padding-left: 40px;
        background-position: 4px center;
      }

    }

    .course__input {
      margin: 15px auto;
    }

  }

}