@charset "UTF-8";

/**
 * Основные стили
 **/

body {
  font-family: 'Open Sans', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-size: 16px;
}

.container {
  @include min-max(320px, 2560px, 0);
}

.wrap {
  @include min-max(990px, 1200px, 15px);
}

.title-courses {
  font-family: Montserrat;
  font-weight: 800;
  line-height: 55px;
  font-size: 45px;
  text-align: center;
  margin: 50px auto 30px;
  max-width: 890px;
}

.courses {
  background-color: #F8F9FB;
  padding-top: 10px;
}