.files {
  margin: 50px 0;

  .files__title {
    font-size: 50px;
    font-weight: 300;
    line-height: 60px;
    margin: 15px 0;
    text-align: center;
  }

  .files__block {
    display: flex;
    justify-content: center;
  }

  .files__element {
    flex-basis: 180px;
    text-align: center;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin: 30px 40px;

    p {
      font-size: 20px;
      line-height: 20px;
      font-weight: 300;
      margin: 20px 0;

      span {
        font-size: 16px;
        line-height: 18px;
        display: inline-block;
      }

    }

    a {
      background-color: $green;
      border-radius: 19px;
      text-align: center;
      font-size: 20px;
      display: block;
      line-height: 27px;
      padding: 5px 0;
      width: 153px;
      margin: 0 auto;
      color: $white;
      text-decoration: none;
      font-weight: 300;

      &:hover {
        background-color: darken($green, 10%);
      }

    }

  }

}